.loader {
  display: -ms-flexbox;
  display: flex;
  height: 34px;
  -ms-flex-align: center;
      align-items: center;
}

.loader-dot {
  margin-right: 12px;
  -webkit-animation: bounce 1.4s infinite ease-in-out both;
          animation: bounce 1.4s infinite ease-in-out both;
}

.loader-dot:nth-child(1) { -webkit-animation-delay: -0.96s; animation-delay: -0.96s; }
.loader-dot:nth-child(2) { -webkit-animation-delay: -0.80s; animation-delay: -0.80s; }
.loader-dot:nth-child(3) { -webkit-animation-delay: -0.64s; animation-delay: -0.64s; }
.loader-dot:nth-child(4) { -webkit-animation-delay: -0.48s; animation-delay: -0.48s; }
.loader-dot:nth-child(5) { -webkit-animation-delay: -0.32s; animation-delay: -0.32s; }
.loader-dot:nth-child(6) { -webkit-animation-delay: -0.16s; animation-delay: -0.16s; }

@-webkit-keyframes bounce {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}

@keyframes bounce {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  } 40% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
  }
}



